import { studentProfileApi } from "./studentProfileApi";
import {
  studentAdmissionFeeListFunction,
  studentAdmissionFeeCountFunction,
  studentOneProfileFunction,
  studentAdmissionFeeCardAddScholarshipNumberFunction,
  studentAdmissionFeeCardValidateScholarshipNumberFunction,
  studentAdmissionFeeCardAddRemarkFunction,
  studentAdmissionFeeCardDropFunction,
  studentAdmissionFeeCardUpdateStructureFunction,
  studentAdmissionFeeCardInstallmentRemoveFunction,
  studentAdmissionFeeCardCollectFunction,
  studentAdmissionFeeCardExcessRefundFunction,
  studentAdmissionFeeCardScholarshipCollectFunction,
} from "./student_profile_manager_query_mutation_function";

const studentProfileApiDynamic = studentProfileApi.injectEndpoints({
  endpoints: (builder) => ({
    studentAdmissionFeeList: studentAdmissionFeeListFunction(builder),
    studentAdmissionFeeCount: studentAdmissionFeeCountFunction(builder),
    studentOneProfile: studentOneProfileFunction(builder),
    studentAdmissionFeeCardAddScholarshipNumber:
      studentAdmissionFeeCardAddScholarshipNumberFunction(builder),
    studentAdmissionFeeCardValidateScholarshipNumber:
      studentAdmissionFeeCardValidateScholarshipNumberFunction(builder),
    studentAdmissionFeeCardAddRemark:
      studentAdmissionFeeCardAddRemarkFunction(builder),
    studentAdmissionFeeCardDrop: studentAdmissionFeeCardDropFunction(builder),
    studentAdmissionFeeCardUpdateStructure:
      studentAdmissionFeeCardUpdateStructureFunction(builder),
    studentAdmissionFeeCardInstallmentRemove:
      studentAdmissionFeeCardInstallmentRemoveFunction(builder),
    studentAdmissionFeeCardCollect:
      studentAdmissionFeeCardCollectFunction(builder),
    studentAdmissionFeeCardExcessRefund:
      studentAdmissionFeeCardExcessRefundFunction(builder),
    studentAdmissionFeeCardScholarshipCollect:
      studentAdmissionFeeCardScholarshipCollectFunction(builder),
    // studentAdmissionFeeListFunction: studentAdmissionFeeListFunction(builder),
    // studentAdmissionFeeListFunction: studentAdmissionFeeListFunction(builder),
    // studentAdmissionFeeListFunction: studentAdmissionFeeListFunction(builder),
  }),
});

export const {
  useStudentAdmissionFeeListQuery,
  useStudentAdmissionFeeCountQuery,
  useStudentOneProfileQuery,
  useStudentAdmissionFeeCardAddScholarshipNumberMutation,
  useStudentAdmissionFeeCardValidateScholarshipNumberMutation,
  useStudentAdmissionFeeCardAddRemarkMutation,
  useStudentAdmissionFeeCardDropMutation,
  useStudentAdmissionFeeCardUpdateStructureMutation,
  useStudentAdmissionFeeCardInstallmentRemoveMutation,
  useStudentAdmissionFeeCardCollectMutation,
  useStudentAdmissionFeeCardExcessRefundMutation,
  useStudentAdmissionFeeCardScholarshipCollectMutation,
} = studentProfileApiDynamic;
