import { useTranslation } from "react-i18next";
import style from "./StudentAdmissionFees.module.css";
import { assestsUserAdmissionUrl } from "../../Services/UrlConfig/AssestsBaseUrl";

const StudentAdmissionFeesCardTotal = ({ admission }) => {
  const { t } = useTranslation();
  return (
    <>
      <section className={style.student_admission_count_container}>
        <div className={style.student_admission_count}>
          <h5
            style={{
              color: "#121212",
            }}
          >
            {t("rs_only")}
            {admission?.fee_structure?.total_admission_fees ?? 0}
            <span
              style={{
                fontSize: "13px",
                fontWeight: "400",
                color: "#121212",
              }}
            >
              {` (${t("rs_only")}${
                admission?.fee_structure?.applicable_fees ?? 0
              })`}
            </span>
          </h5>
          <h6
            style={{
              marginBottom: "1rem",
            }}
          >
            {t("total_applicable_fees")}
          </h6>

          <h5
            style={{
              color: "#008000",
            }}
          >
            {t("rs_only")} {admission?.paid_fee ?? 0}
            <span
              style={{
                fontSize: "13px",
                fontWeight: "400",
                color: "#121212",
              }}
            >
              {` (${t("rs_only")}${
                admission?.fee_structure?.applicable_fees -
                  admission?.paid_fee >
                0
                  ? admission?.fee_structure?.applicable_fees -
                    admission?.paid_fee
                  : 0
              })`}
            </span>
          </h5>
          <h6>{t("paid_and_outstanding")}</h6>
        </div>
        <div className={style.student_admission_count_image}>
          {admission?.status === "Paid" ? (
            admission?.paid_fee - admission?.applicable_fee > 0 ? (
              <>
                <img
                  src={`${assestsUserAdmissionUrl}/fees-refund.svg`}
                  alt="pending icon"
                />
                <div className={style.show_text}>
                  <h6
                    style={{
                      fontWeight: "600",
                    }}
                  >
                    {t("rs_only")}{" "}
                    {admission?.paid_fee - admission?.applicable_fee}
                  </h6>
                  <h6> {t("excess")}</h6>
                  <button className={style.image_refund_btn}>
                    {t("refund")}
                  </button>
                </div>
              </>
            ) : (
              <img
                src={`${assestsUserAdmissionUrl}/fees-paid.svg`}
                alt="pending icon"
                style={{
                  width: "140px",
                  height: "140px",
                }}
              />
            )
          ) : admission?.remaining_fee ? (
            <>
              <img
                src={`${assestsUserAdmissionUrl}/fees-pending.svg`}
                alt="pending icon"
                style={{
                  width: "140px",
                  height: "140px",
                }}
              />
              <div className={style.show_text}>
                <h6
                  style={{
                    fontWeight: "600",
                  }}
                >
                  {t("rs_only")} {admission?.remaining_fee ?? 0}
                </h6>
                <h6> {t("pending")}</h6>
              </div>
            </>
          ) : (
            ""
          )}
        </div>
      </section>
    </>
  );
};

export default StudentAdmissionFeesCardTotal;
