import { useState } from "react";
import style from "./StudentAdmissionFees.module.css";
import { useTranslation } from "react-i18next";
import AddStudentAdmissionFeesCardRemark from "./Menus/AddStudentAdmissionFeesCardRemark";
import AddButton from "../../Ui/Button/AddButton";
import AddButtonWrapper from "../../Ui/Button/AddButtonWrapper";

const StudentAdmissionFeesCardRemark = ({ admission }) => {
  const { t } = useTranslation();
  const [step, setStep] = useState("");
  const onCloseStep = () => {
    setStep("");
  };
  const onEditMenu = () => {
    setStep(1);
  };

  return (
    <>
      <div className={style.remark_card_container}>
        <section className={style.add_remark}>
          <AddButtonWrapper>
            <AddButton
              label={t(admission?.remark ? "edit_remark" : "add_remark")}
              onAction={onEditMenu}
              customStyle={{
                padding: "0.2rem 0.4rem",
                fontSize: "11px",
                fontWeight: "400",
              }}
            />
          </AddButtonWrapper>

          <h6 className={style.add_remark_text}>
            {t("fee_remark")} {admission?.remark ?? ""}
          </h6>
        </section>
      </div>
      {step === 1 ? (
        <AddStudentAdmissionFeesCardRemark
          onClose={onCloseStep}
          rcid={admission?._id}
          remark={admission?.remark ?? ""}
        />
      ) : null}
    </>
  );
};

export default StudentAdmissionFeesCardRemark;
