import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/Income/Income.module.css";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import CreateButton from "../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../Loader/QvipleLoader";
import FormInput from "../../../Ui/Input/FormInput";
import {
  useStudentAdmissionFeeCardAddScholarshipNumber,
  useStudentAdmissionFeeCardValidateScholarshipNumber,
} from "../../../Hooks/ApiHooks/StudentProfile/student-manager-api";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import NotifyEvent from "../../../Utils/Snackbar/NotifyEvent";

const AddStudentAdmissionFeesCardScholarshipNumber = ({
  onClose,
  setScholarNumber,
  scholarNumber,
  rcid,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState(scholarNumber ?? "");
  const [errorField, setErrorField] = useState(false);
  const [snackbar, setSnackbar] = useState({
    open: false,
    msg: "",
    variant: "error",
  });

  const [studentAdmissionFeeCardAddScholarshipNumber] =
    useStudentAdmissionFeeCardAddScholarshipNumber();
  const [studentAdmissionFeeCardValidateScholarshipNumber] =
    useStudentAdmissionFeeCardValidateScholarshipNumber();

  useEffect(() => {
    if (scholarNumber) {
      setState(scholarNumber);
    }
  }, [scholarNumber]);

  const onUpdate = () => {
    let validation = requiredField({ query: state }, []);
    if (existingRequiredField(validation)) {
      setDisabled((pre) => !pre);
      studentAdmissionFeeCardValidateScholarshipNumber({
        num: state,
      })
        .then((res) => {
          if (!res.data?.count) {
            studentAdmissionFeeCardAddScholarshipNumber({
              rcid: rcid,
              scholarData: {
                scholar_ship_number: state,
              },
            })
              .then(() => {
                setScholarNumber(state);
                setDisabled((pre) => !pre);
                onClose();
              })
              .catch({});
          } else {
            setSnackbar({
              msg: "This scholarship number already added",
              open: true,
              variant: "error",
            });
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };
  return (
    <>
      <Modal>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("scholarship_application_no")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormInput
              placeholder={t("scholarship_application_no_placeholder")}
              name="scholar"
              value={state}
              type="text"
              onChange={(e) => setState(e.target.value)}
              errorShow={errorField["query"]}
            />

            <CreateButton label="update" onAction={onUpdate} />
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
      <NotifyEvent eventState={snackbar} eventStateHandler={setSnackbar} />
    </>
  );
};

export default AddStudentAdmissionFeesCardScholarshipNumber;
