import { decryption } from "../Encryption/decrypt";
import { DELETE, PATCH, POST } from "../UrlConfig/MethodType";

// dashboard
export const admissionDashboardMasterFunction = (builder) => {
  return builder.query({
    query: (data) =>
      `/admission/${data.aid}/dashboard/query?mod_id=${data.mod_id}`,
    // transformResponse: (response) => decryption(response?.encrypt),
  });
};

// offline payment verification
export const admissionPaymentVerifyListFunction = (builder) => {
  return builder.query({
    query: (data) =>
      `/admission/${data.aid}/all/receipts/by?page=${data.page}&limit=${data.limit}&filter_by=${data.filterBy}&over_filter=${data.over_filter}&search=${data.search}`,
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    transformResponse: (response, meta, arg) => {
      if (arg?.search)
        return {
          search: true,
          list: response?.all_requests,
        };
      else
        return {
          page: arg?.page,
          list: response?.all_requests,
        };
    },
    merge: (currentCache, incomingData) => {
      if (incomingData?.search) return incomingData;
      else if (incomingData?.page === 1) return incomingData;
      else return void currentCache?.list?.push(...incomingData?.list);
    },

    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};

export const admissionPaymentVerifyActionFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/admission/${data.aid}/one/receipts/${data.rid}/status/decheque/query?reqId=${data.reqId}&status=${data.status}`,
      method: PATCH,
      body: data.receiptAction,
    }),
  });
};

// here start student admission fees
export const studentAdmissionFeeListFunction = (builder) => {
  return builder.query({
    query: (sid) => `/admission/${sid}/fees`,
    providesTags: ["Admission_Fees"],
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    transformResponse: (response) => {
      let dect = decryption(response?.encrypt);
      // console.info("dslkknfbsnmd", dect);
      return dect?.array;
    },
    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};
export const studentAdmissionFeeCountFunction = (builder) => {
  return builder.query({
    query: (sid) => `/fees/student/${sid}`,
    providesTags: ["Admission_Fees"],
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    transformResponse: (response) => {
      // let dect = decryption(response?.encrypt);
      // console.info("dslkknfbsnmd", dect);
      return response?.student?.admission_amount_stats;
    },
    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};

