import { useStudentAdmissionFeeCardUpdateStructure } from "../../Hooks/ApiHooks/StudentProfile/student-manager-api";
import QvipleLoader from "../../Loader/QvipleLoader";
import SelectFeeStructure from "../../UserStaffMember/FinanceManager/FeesStructure/SelectFeeStructure/SelectFeeStructure";
import { useState } from "react";

const UpdateStudentAdmissionFeesCardStructure = ({
  structureId,
  onClose,
  aid,
  appId,
  sid,
  rid,
  did,
  bid,
  cmid,
  onParentClose,
}) => {
  const { disabled, setDisabled } = useState(false);
  const [studentAdmissionFeeCardUpdateStructure] =
    useStudentAdmissionFeeCardUpdateStructure();
  const onSelectedStrucutre = (val) => {
    if (structureId === val?._id) {
    } else {
      if (aid && sid && appId && structureId && rid) {
        setDisabled((pre) => !pre);
        studentAdmissionFeeCardUpdateStructure({
          aid: aid,
          appId: appId,
          sid: sid,
          updateStrucutre: {
            old_fee_struct: structureId,
            new_fee_struct: val?._id,
            rid: rid,
          },
        })
          .then(() => {
            onParentClose();
            setDisabled((pre) => !pre);
          })
          .catch({});
      }
    }
  };

  return (
    <>
      <SelectFeeStructure
        onClose={onClose}
        did={did}
        bid={bid}
        cmid={cmid}
        selectedStructure={structureId}
        onSelectedStrucutre={onSelectedStrucutre}
      />
      {disabled && <QvipleLoader />}
    </>
  );
};

export default UpdateStudentAdmissionFeesCardStructure;
