import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/Income/Income.module.css";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import CreateButton from "../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { useStudentAdmissionFeeCardAddRemark } from "../../../Hooks/ApiHooks/StudentProfile/student-manager-api";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import FormTextarea from "../../../Ui/Input/FormTextarea";

const AddStudentAdmissionFeesCardRemark = ({ onClose, remark, rcid }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState(remark ?? "");
  const [errorField, setErrorField] = useState(false);

  const [studentAdmissionFeeCardAddRemark] =
    useStudentAdmissionFeeCardAddRemark();

  useEffect(() => {
    if (remark) {
      setState(remark);
    }
  }, [remark]);

  const onUpdate = () => {
    let validation = requiredField({ query: state }, []);
    if (existingRequiredField(validation)) {
      setDisabled((pre) => !pre);
      studentAdmissionFeeCardAddRemark({
        rcid: rcid,
        remark: {
          remark: state,
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };
  return (
    <>
      <Modal>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("remark_title")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormTextarea
              label={t("enter_remark")}
              placeholder={t("please_enter_remark")}
              name="scholar"
              value={state}
              type="text"
              onChange={(e) => setState(e.target.value)}
              errorShow={errorField["query"]}
            />

            <CreateButton label="save_remark" onAction={onUpdate} />
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default AddStudentAdmissionFeesCardRemark;
