import { useState } from "react";
import {
  assestsUserAdmissionUrl,
  assestsUserFinanceUrl,
} from "../../Services/UrlConfig/AssestsBaseUrl";
import style from "./StudentAdmissionFees.module.css";
import StudentAdmissionFeesCardScholarshipNumber from "./StudentAdmissionFeesCardScholarshipNumber";
import StudentAdmissionFeesCardTotal from "./StudentAdmissionFeesCardTotal";
import StudentAdmissionFeesCardInstallmentCard from "./StudentAdmissionFeesCardInstallmentCard";
import StudentAdmissionFeesCardRemark from "./StudentAdmissionFeesCardRemark";
import { useTranslation } from "react-i18next";
import BorderBottom from "../../Ui/Border/BorderBottom";
import StudentAdmissionFeesCardSubTotal from "./StudentAdmissionFeesCardSubTotal";
import StudentAdmissionFeesCardInstallmentCardGov from "./StudentAdmissionFeesCardInstallmentCardGov";
import StudentAdmissionFeesCardMenu from "./Menus/StudentAdmissionFeesCardMenu";

const StudentAdmissionFeesCard = ({ admission }) => {
  const { t } = useTranslation();
  const [step, setStep] = useState("");
  const [collapse, setCollapse] = useState(true);
  const onCloseStep = () => {
    setStep("");
  };
  const onEditMenu = () => {
    setStep(1);
  };
  const onCollapse = () => {
    setCollapse((pre) => !pre);
  };
  // console.info("admission", admission);

  return (
    <>
      <section className={style.af_container_inner}>
        <section className={style.student_admission_card_title}>
          <h6>
            {admission?.card_type === "Promote"
              ? admission?.student?.studentClass?.classTitle ?? ""
              : admission?.appId?.applicationName ?? ""}
          </h6>
          <p>{admission?.fee_structure?.unique_structure_name ?? ""}</p>
          <img
            src={`${assestsUserFinanceUrl}/menu.svg`}
            alt="menu icon"
            onClick={onEditMenu}
            className={style.edit_icon}
            title="Menu"
          />
        </section>
        <StudentAdmissionFeesCardScholarshipNumber
          scholarNumber={admission?.scholar_ship_number}
          rcid={admission?._id}
        />
        <StudentAdmissionFeesCardTotal admission={admission} />
        <div className={style.afc_collapse}>
          <span onClick={onCollapse}>
            {t(collapse ? "expand" : "collapse")}
          </span>
          <img
            src={
              collapse
                ? `${assestsUserAdmissionUrl}/collapse.svg`
                : `${assestsUserAdmissionUrl}/expand.svg`
            }
            alt="collapse icon"
            onClick={onCollapse}
          />
        </div>
        {!collapse ? (
          <>
            <BorderBottom />

            <h5 className={style.af_heading}>{t("from_student")}</h5>
            <StudentAdmissionFeesCardSubTotal
              title1="o_total"
              amount1={admission?.applicable_card?.applicable_fee}
              title2="paid"
              amount2={admission?.applicable_card?.paid_fee}
              title3="outstanding"
              amount3={admission?.applicable_card?.remaining_fee}
            />
            {admission?.applicable_card?.remaining_array?.map((install) => (
              <StudentAdmissionFeesCardInstallmentCard
                install={install}
                key={install?._id}
                admission={admission}
              />
            ))}
            <h5
              className={style.af_heading}
              style={{
                marginTop: "1.5rem",
              }}
            >
              <BorderBottom />
              {t("from_gov_scholarship")}
            </h5>
            <StudentAdmissionFeesCardSubTotal
              title1="o_total"
              amount1={admission?.government_card?.applicable_fee}
              title2="received"
              amount2={admission?.government_card?.paid_fee}
              title3="outstanding"
              amount3={admission?.government_card?.remaining_fee}
            />

            {admission?.government_card?.remaining_array?.map((install) => (
              <StudentAdmissionFeesCardInstallmentCardGov
                install={install}
                key={install?._id}
                admission={admission}
              />
            ))}
            <StudentAdmissionFeesCardRemark admission={admission} />
          </>
        ) : null}
      </section>
      {step === 1 ? (
        <StudentAdmissionFeesCardMenu
          onClose={onCloseStep}
          rid={admission?._id}
          sid={admission?.student?._id}
          is_drop={admission?.drop_status === "Disable" ? false : true}
          structureId={admission?.fee_structure?._id}
          aid={admission?.appId?.admissionAdmin?._id}
          appId={admission?.appId?._id}
          did={admission?.fee_structure?.department}
          bid={admission?.fee_structure?.batch_master}
          cmid={admission?.fee_structure?.class_master?._id}
        />
      ) : null}
    </>
  );
};

export default StudentAdmissionFeesCard;
